import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import SupplierCreateProfileForm from "./createProfileForm";
import notification from "../../../../components/notification";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/supplier/supplier/actions";
import supplierTypeActions from "../../../../redux/supplier/supplierType/actions";
import taxActions from "../../../../redux/tax/tax/actions";
import paymentCategoryActions from "../../../../redux/payment/paymentCategory/actions";
import paymentTermActions from "../../../../redux/payment/paymentTerm/actions";

import { Box } from "../../../App/app.style";
import { Tabs } from "antd";

const TabPane = Tabs.TabPane;
const redirectTo = "/dashboard/setup/suppliers";
const { storeSupplierType } = supplierTypeActions;
const { storeTax } = taxActions;
const { storePaymentCategory } = paymentCategoryActions;
const { storePaymentTerm } = paymentTermActions;

class CreateSupplier extends Component {
  state = {
    visibleAddSupplierType: false,
    visibleAddTax: false,
    visibleAddPaymentCategory: false,
    visibleAddPaymentTerm: false
  };
  // Get All data diawal
  componentDidMount() {
    const { createSupplier } = this.props;
    createSupplier();
  }

  componentDidUpdate() {
    // Display error notif Supplier
    if (this.props.supplier.error) {
      notification("error", this.props.supplier.error);
      this.props.clearError();
    }

    if (this.props.supplier.success) {
      this.props.history.replace(
        redirectTo + "/update/" + this.props.supplier.id_supplier
      );
    }

    // Supplier Type
    if (this.props.supplierType.error) {
      notification("error", this.props.supplierType.error);
      this.props.clearError();
    }

    if (this.props.supplierType.success) {
      const { createSupplier, clearSuccess } = this.props;
      notification("success", this.props.supplierType.success);
      this.handleModalAddSupplierType();
      clearSuccess();
      createSupplier();
    }

    // Tax
    if (this.props.tax.error) {
      notification("error", this.props.tax.error);
      this.props.clearError();
    }

    if (this.props.tax.success) {
      const { createSupplier, clearSuccess } = this.props;
      notification("success", this.props.tax.success);
      this.handleModalAddTax();
      clearSuccess();
      createSupplier();
    }

    // Payment Category
    if (this.props.paymentCategory.error) {
      notification("error", this.props.paymentCategory.error);
      this.props.clearError();
    }

    if (this.props.paymentCategory.success) {
      const { createSupplier, clearSuccess } = this.props;
      notification("success", this.props.paymentCategory.success);
      this.handleModalAddPaymentCategory();
      clearSuccess();
      createSupplier();
    }

    // Payment Term
    if (this.props.paymentTerm.error) {
      notification("error", this.props.paymentTerm.error);
      this.props.clearError();
    }

    if (this.props.paymentTerm.success) {
      const { createSupplier, clearSuccess } = this.props;
      notification("success", this.props.paymentTerm.success);
      this.handleModalAddPaymentTerm();
      clearSuccess();
      createSupplier();
    }
  }

  handleModalAddSupplierType = () => {
    this.setState({
      visibleAddSupplierType: !this.state.visibleAddSupplierType
    });
  };

  handleModalAddTax = () => {
    this.setState({
      visibleAddTax: !this.state.visibleAddTax
    });
  };

  handleModalAddPaymentCategory = () => {
    this.setState({
      visibleAddPaymentCategory: !this.state.visibleAddPaymentCategory
    });
  };

  handleModalAddPaymentTerm = () => {
    this.setState({
      visibleAddPaymentTerm: !this.state.visibleAddPaymentTerm
    });
  };

  handleSubmit = value => {
    const { storeSupplier } = this.props;
    //value.start_date = value.start_date.format("YYYY-MM-DD");
    value.discount = value.discount / 100;
    storeSupplier(value);
  };

  handleSubmitSupplierType = value => {
    const { storeSupplierType } = this.props;
    storeSupplierType(value);
  };

  handleSubmitTax = value => {
    const { storeTax } = this.props;
    storeTax(value);
  };

  handleSubmitPaymentCategory = value => {
    const { storePaymentCategory } = this.props;
    storePaymentCategory(value);
  };

  handleSubmitPaymentTerm = value => {
    const { storePaymentTerm } = this.props;
    storePaymentTerm(value);
  };

  render() {
    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="suppliers.add.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <Tabs defaultActiveKey="1">
            <TabPane tab={<IntlMessages id="suppliers.tab.supplier" />} key="1">
              <SupplierCreateProfileForm
                auth={this.props.auth}
                submit={this.handleSubmit}
                supplier={this.props.supplier}
                //Supplier Type
                supplierType={this.props.supplierType}
                visibleAddSupplierType={this.state.visibleAddSupplierType}
                handleModalAddSupplierType={this.handleModalAddSupplierType}
                handleSubmitSupplierType={this.handleSubmitSupplierType}
                // Tax
                tax={this.props.tax}
                visibleAddTax={this.state.visibleAddTax}
                handleModalAddTax={this.handleModalAddTax}
                handleSubmitTax={this.handleSubmitTax}
                // Payment Category
                paymentCategory={this.props.paymentCategory}
                visibleAddPaymentCategory={this.state.visibleAddPaymentCategory}
                handleModalAddPaymentCategory={
                  this.handleModalAddPaymentCategory
                }
                handleSubmitPaymentCategory={this.handleSubmitPaymentCategory}
                // Payment Term
                paymentTerm={this.props.paymentTerm}
                visibleAddPaymentTerm={this.state.visibleAddPaymentTerm}
                handleModalAddPaymentTerm={this.handleModalAddPaymentTerm}
                handleSubmitPaymentTerm={this.handleSubmitPaymentTerm}
              />
            </TabPane>
          </Tabs>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.auth,
    supplier: state.supplier,
    supplierType: state.supplierType,
    tax: state.tax,
    paymentCategory: state.paymentCategory,
    paymentTerm: state.paymentTerm
  };
};

export default compose(
  connect(mapStateToProps, {
    ...actions,
    storeSupplierType,
    storeTax,
    storePaymentCategory,
    storePaymentTerm
  })
)(CreateSupplier);
