import React, { Component } from "react";
import moment from "moment";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import {
  Empty,
  Form,
  Input,
  InputNumber,
  Row,
  Col,
  Checkbox,
  Select,
  Spin
} from "antd";
import { getCurrencyRate } from "../../../../helpers/helper";
import getLanguage from "../../../App/MyLanguange";
import ModalAddSupplierType from "./modalAddSupplierType";
import ModalAddTax from "./modalAddTax";
import ModalAddPaymentCategory from "./modalAddPaymentCategory";
import ModalAddPaymentTerm from "./modalAddPaymentTerm";

const { Option } = Select;
const { TextArea } = Input;
const InputGroup = Input.Group;

class SupplierCreateProfilForm extends Component {
  state = {
    check_code: false,
    currency_is_base: false
  };

  handleChangeCode = () => {
    const { setFieldsValue } = this.props.form;
    this.setState({ check_code: !this.state.check_code });
    if (this.state.check_code === false) {
      setFieldsValue({
        ["supplier_code"]: null
      });
    }
  };

  handleChangeLocation = () => {
    const { setFieldsValue } = this.props.form;
    setFieldsValue({
      id_payment_category: null,
      id_payment_term: null
    });
  };

  handleChangePaymentCategory = () => {
    const { setFieldsValue } = this.props.form;
    setFieldsValue({
      id_payment_term: null
    });
  };

  handleSubmit = e => {
    //console.log("Masuk Pak")
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        this.props.submit(values);
      }
    });
  };

  setFieldRate = (id_currency, date) => {
    const { currencies } = this.props.supplier;
    const { setFieldsValue } = this.props.form;

    currencies.map(data => {
      if (data.id_currency === id_currency) {
        this.setState({
          currency_is_base: data.is_base
        });

        let rate = getCurrencyRate(data.currency_rate, date);
        setFieldsValue({
          ob_rate: rate
        });
      }
    });
  };

  handleChangeStartDate = value => {
    const { getFieldValue } = this.props.form;
    if (getFieldValue("id_currency") && value instanceof moment) {
      this.setFieldRate(getFieldValue("id_currency"), value);
    }
  };

  handleChangeCurrency = value => {
    const { getFieldValue, setFieldsValue } = this.props.form;
    const { accounts, default_id_payable_account } = this.props.supplier;
    let id_account = null;
    if (accounts.length > 0) {
      id_account = accounts
        .filter(x => x.id_currency === value)
        .find(x => x.id_account === default_id_payable_account)
        ? default_id_payable_account
        : null;

      //console.log(id_account)
    }
    this.setFieldRate(value, getFieldValue("start_date"));
    setFieldsValue({ id_account });
  };

  render() {
    const { getFieldDecorator, getFieldError, getFieldValue } = this.props.form;
    const { supplier } = this.props;
    const { loading, loadingSubmit, payment_categories } = supplier;
    const error = supplier.errorField ? supplier.errorField : [];
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <>
        <ModalAddSupplierType
          handleModal={this.props.handleModalAddSupplierType}
          supplierType={this.props.supplierType}
          submit={this.props.handleSubmitSupplierType}
          visible={this.props.visibleAddSupplierType}
        />
        <ModalAddTax
          handleModal={this.props.handleModalAddTax}
          tax={this.props.tax}
          supplier={this.props.supplier}
          submit={this.props.handleSubmitTax}
          visible={this.props.visibleAddTax}
        />
        <ModalAddPaymentCategory
          handleModal={this.props.handleModalAddPaymentCategory}
          paymentCategory={this.props.paymentCategory}
          supplier={this.props.supplier}
          submit={this.props.handleSubmitPaymentCategory}
          visible={this.props.visibleAddPaymentCategory}
        />
        <ModalAddPaymentTerm
          handleModal={this.props.handleModalAddPaymentTerm}
          paymentTerm={this.props.paymentTerm}
          submit={this.props.handleSubmitPaymentTerm}
          visible={this.props.visibleAddPaymentTerm}
          is_cash={
            getFieldValue("id_payment_category")
              ? payment_categories.find(
                  cate =>
                    cate.id_payment_category ===
                    getFieldValue("id_payment_category")
                ).payment_type === 2
                ? 0
                : 1
              : null
          }
        />
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          <div className="isoDetailDataFlex" style={{ padding: "0" }}>
            <div
              className="isoColumn-2 isoFormGroupComp"
              style={{ padding: "0 15px 0 0" }}
            >
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="supplierTypes.pageHeader" />}
              >
                {getFieldDecorator("id_supplier_type", {
                  //initialValue: 0,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Select
                    loading={this.props.supplier.loading}
                    showSearch
                    optionFilterProp="children"
                  >
                    {permissions.includes("Create Supplier Type") ? (
                      <Option
                        key={"Create Supplier Type"}
                        value={undefined}
                        onClick={() => this.props.handleModalAddSupplierType()}
                      >
                        <div style={{ color: "#1890FF", fontStyle: "italic" }}>
                          {" + " +
                            getLanguage({
                              id: "supplierTypes.pageHeader"
                            })}
                        </div>
                      </Option>
                    ) : null}

                    {this.props.supplier.supplier_types.map((data, key) => (
                      <Option key={key} value={data.id_supplier_type}>
                        {data.supplier_type_code +
                          " (" +
                          data.supplier_type_name +
                          ")"}
                      </Option>
                    ))}
                  </Select>
                )}
              </Form.Item>

              <InputGroup compact>
                <Col span={20}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="suppliers.add.code" />}
                    validateStatus={
                      error.supplier_code || getFieldError("supplier_code")
                        ? "error"
                        : null
                    }
                    help={
                      error.supplier_code
                        ? error.supplier_code
                        : getFieldError("supplier_code")
                    }
                  >
                    {getFieldDecorator("supplier_code", {
                      // initialValue: data.supplier_code,
                      rules: [
                        {
                          max: 30,
                          message: <IntlMessages id="maxLength30" />
                        }
                      ]
                    })(
                      <Input disabled={this.state.check_code ? true : false} />
                    )}
                  </Form.Item>
                </Col>
                <Col span={4}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="autoGenerateCode" />}
                  >
                    <Checkbox
                      style={{
                        paddingTop: "5px",
                        paddingLeft: "8px"
                      }}
                      checked={this.state.check_code}
                      onChange={this.handleChangeCode}
                    />
                  </Form.Item>
                </Col>
              </InputGroup>

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="suppliers.add.name" />}
              >
                {getFieldDecorator("supplier_name", {
                  // initialValue: data.supplier_name,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    },
                    {
                      max: 50,
                      message: <IntlMessages id="maxLength50" />
                    }
                  ]
                })(<Input />)}
              </Form.Item>

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="suppliers.filter.registeredAt" />}
              >
                {getFieldDecorator("id_location", {
                  //initialValue: 0,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Select
                    loading={this.props.supplier.loading}
                    showSearch
                    optionFilterProp="children"
                    onChange={this.handleChangeLocation}
                  >
                    {this.props.supplier.locations.map((data, key) => (
                      <Option key={key} value={data.id_location}>
                        {data.location_code + " (" + data.location_name + ")"}
                      </Option>
                    ))}
                  </Select>
                )}
              </Form.Item>

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="suppliers.filter.employee" />}
              >
                {getFieldDecorator("id_related_employee", {
                  //initialValue: 0,
                  // rules: [
                  //   {
                  //     required: true,
                  //     message: <IntlMessages id="required" />
                  //   }
                  // ]
                })(
                  <Select
                    loading={this.props.supplier.loading}
                    showSearch
                    optionFilterProp="children"
                    allowClear={true}
                    //disabled={used ? true : false}
                  >
                    {this.props.supplier.employees.map((data, key) => (
                      <Option key={key} value={data.id_employee}>
                        {data.employee_code +
                          " (" +
                          data.first_name +
                          (data.last_name ? " " + data.last_name : "") +
                          ")"}
                      </Option>
                    ))}
                  </Select>
                )}
              </Form.Item>

              <InputGroup compact>
                <Col span={12}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="paymentTerms.add.discount" />}
                  >
                    {getFieldDecorator("discount", {
                      initialValue: 0,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(
                      <InputNumber
                        //disabled={used ? true : false}
                        style={{ width: "100%" }}
                        min={0}
                        max={100}
                        formatter={value => `${value}%`}
                        parser={value => value.replace("%", "")}
                      />
                    )}
                  </Form.Item>
                </Col>
                <Col span={12}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="leadTime" />}
                  >
                    {getFieldDecorator("lead_time", {
                      // initialValue: 0,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(
                      <InputNumber
                        //disabled={used ? true : false}
                        style={{ width: "100%" }}
                        min={0}
                      />
                    )}
                  </Form.Item>
                </Col>
              </InputGroup>
            </div>
            <div
              className="isoColumn-2 isoFormGroupComp"
              style={{ padding: "0 0 0 15px" }}
            >
              <InputGroup compact>
                <Col span={12}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="currency" />}
                  >
                    {getFieldDecorator("id_currency", {
                      //initialValue: 0,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(
                      <Select
                        loading={this.props.supplier.loading}
                        showSearch
                        optionFilterProp="children"
                        style={{ width: "100%" }}
                        onChange={this.handleChangeCurrency}
                        //disabled={used ? true : false}
                      >
                        {this.props.supplier.currencies.map((data, key) => (
                          <Option key={key} value={data.id_currency}>
                            {data.currency_code +
                              " (" +
                              data.currency_name +
                              ")"}
                          </Option>
                        ))}
                      </Select>
                    )}
                  </Form.Item>
                </Col>
                <Col span={12}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="suppliers.add.creditLimit" />}
                  >
                    {getFieldDecorator("credit_limit", {
                      initialValue: 0,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(
                      <InputNumber
                        formatter={value =>
                          `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                        }
                        parser={value => value.replace(/\$\s?|(,*)/g, "")}
                        min={0}
                        //disabled={used ? true : false}
                        style={{ width: "100%" }}
                        precision={2}
                      />
                    )}
                  </Form.Item>
                </Col>
              </InputGroup>

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="suppliers.filter.tax" />}
              >
                {getFieldDecorator("id_tax", {
                  //initialValue: 0,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Select
                    loading={this.props.supplier.loading}
                    showSearch
                    optionFilterProp="children"
                  >
                    {permissions.includes("Create Tax") ? (
                      <Option
                        key={"Create Tax"}
                        value={undefined}
                        onClick={() => this.props.handleModalAddTax()}
                      >
                        <div style={{ color: "#1890FF", fontStyle: "italic" }}>
                          {" + " +
                            getLanguage({
                              id: "taxes.pageHeader"
                            })}
                        </div>
                      </Option>
                    ) : null}

                    {this.props.supplier.taxes.map((data, key) => (
                      <Option key={key} value={data.id_tax}>
                        {data.tax_name}
                      </Option>
                    ))}
                  </Select>
                )}
              </Form.Item>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="suppliers.add.taxNumber" />}
              >
                {getFieldDecorator("tax_number", {
                  //initialValue: this.state.is_cash===1 ? null : this.state.bank_account_number,
                  rules: [
                    {
                      max: 25,
                      message: <IntlMessages id="maxLength25" />
                    }
                    // {
                    //   pattern: "^[0-9]*$",
                    //   message: <IntlMessages id="taxMustNumber" />
                    // }
                  ]
                })(
                  <Input
                  //addonBefore={is_cash}
                  //style={{ textTransform: "uppercase" }}
                  />
                )}
              </Form.Item>
              <InputGroup compact>
                <Col span={12}>
                  <Form.Item
                    {...formItemLayout}
                    label={
                      <IntlMessages id="suppliers.filter.paymentCategory" />
                    }
                  >
                    {getFieldDecorator("id_payment_category", {
                      //initialValue: 0,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(
                      <Select
                        loading={this.props.supplier.loading}
                        showSearch
                        optionFilterProp="children"
                        onChange={this.handleChangePaymentCategory}
                        style={{ width: "100%" }}
                      >
                        {permissions.includes("Create Payment Category") ? (
                          <Option
                            key={"Create Payment Category"}
                            value={undefined}
                            onClick={() =>
                              this.props.handleModalAddPaymentCategory()
                            }
                          >
                            <div
                              style={{ color: "#1890FF", fontStyle: "italic" }}
                            >
                              {" + " +
                                getLanguage({
                                  id: "paymentCategories.pageHeader"
                                })}
                            </div>
                          </Option>
                        ) : null}

                        {this.props.supplier.payment_categories.map(
                          (data, key) =>
                            data.id_location === null ||
                            data.id_location ===
                              getFieldValue("id_location") ? (
                              <Option
                                key={key}
                                value={data.id_payment_category}
                              >
                                {data.payment_category_code}
                              </Option>
                            ) : null
                        )}
                      </Select>
                    )}
                  </Form.Item>
                </Col>
                <Col span={12}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="suppliers.filter.paymentTerm" />}
                  >
                    {getFieldDecorator("id_payment_term", {
                      //initialValue: 0,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(
                      <Select
                        loading={this.props.supplier.loading}
                        showSearch
                        optionFilterProp="children"
                        //disabled={used ? true : false}
                        notFoundContent={
                          loading ? (
                            <Spin size="small" />
                          ) : getFieldValue("id_payment_category") ? (
                            <Empty description={<IntlMessages id="noData" />} />
                          ) : (
                            <Empty
                              description={
                                <IntlMessages id="selectPaymentCategoryFirst" />
                              }
                            />
                          )
                        }
                        style={{ width: "100%" }}
                      >
                        {getFieldValue("id_payment_category") &&
                        permissions.includes("Create Payment Term") ? (
                          <Option
                            key={"Create Payment Term"}
                            value={undefined}
                            onClick={() =>
                              this.props.handleModalAddPaymentTerm()
                            }
                          >
                            <div
                              style={{ color: "#1890FF", fontStyle: "italic" }}
                            >
                              {" + " +
                                getLanguage({
                                  id: "paymentTerms.pageHeader"
                                })}
                            </div>
                          </Option>
                        ) : null}

                        {getFieldValue("id_payment_category")
                          ? payment_categories.find(
                              cate =>
                                cate.id_payment_category ===
                                getFieldValue("id_payment_category")
                            ).payment_type === 2
                            ? this.props.supplier.payment_terms.map(
                                (data, key) =>
                                  data.is_cash === 0 ? (
                                    <Option
                                      key={key}
                                      value={data.id_payment_term}
                                    >
                                      {data.payment_term_code}
                                    </Option>
                                  ) : null
                              )
                            : this.props.supplier.payment_terms.map(
                                (data, key) =>
                                  data.is_cash === 1 ? (
                                    <Option
                                      key={key}
                                      value={data.id_payment_term}
                                    >
                                      {data.payment_term_code}
                                    </Option>
                                  ) : null
                              )
                          : null}
                      </Select>
                    )}
                  </Form.Item>
                </Col>
              </InputGroup>

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="suppliers.filter.accountPayable" />}
              >
                {getFieldDecorator("id_account", {
                  //initialValue: ,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Select
                    loading={this.props.supplier.loading}
                    showSearch
                    optionFilterProp="children"
                    //disabled={used ? true : false}
                    notFoundContent={
                      loading ? (
                        <Spin size="small" />
                      ) : getFieldValue("id_currency") ? (
                        <Empty description={<IntlMessages id="noData" />} />
                      ) : (
                        <Empty
                          description={
                            <IntlMessages id="select.currencyFirst" />
                          }
                        />
                      )
                    }
                  >
                    {this.props.supplier.accounts.map((data, key) =>
                      data.id_account_type === "09" &&
                      getFieldValue("id_currency") === data.id_currency ? (
                        <Option key={key} value={data.id_account}>
                          {data.account_code + " (" + data.account_name + ")"}
                        </Option>
                      ) : null
                    )}
                  </Select>
                )}
              </Form.Item>
              <Form.Item
                {...formTextArea}
                label={<IntlMessages id="suppliers.add.notes" />}
              >
                {getFieldDecorator("notes", {
                  // initialValue: data.notes
                })(<TextArea autosize={{ minRows: 1 }} />)}
              </Form.Item>
            </div>
          </div>

          <Row>
            <Col>
              <Form.Item {...tailFormItemLayout}>
                <Button
                  type="primary"
                  htmlType="submit"
                  style={{ marginTop: "20px", marginLeft: "50px" }}
                  loading={loading || loadingSubmit}
                >
                  <IntlMessages id="button.submit" />
                </Button>
              </Form.Item>
            </Col>
          </Row>
        </Form>
      </>
    );
  }
}

const WrappedSupplierCreateProfilForm = Form.create({
  name: "create_supplier_profil"
})(SupplierCreateProfilForm);

export default WrappedSupplierCreateProfilForm;
