import React, { Component } from "react";
import IntlMessages from "../../../../components/utility/intlMessages";
import getLanguage from "../../../App/MyLanguange";
import {
  Form,
  Input,
  Col,
  Checkbox,
  Modal,
  Radio,
  Spin,
  Row,
  Select,
  Empty
} from "antd";

const InputGroup = Input.Group;
const Option = Select.Option;

class ModalAddPaymentCategory extends Component {
  state = {
    check_code: false
  };

  componentDidUpdate = () => {
    const { resetFields } = this.props.form;
    const { success } = this.props.paymentCategory;
    if (success) {
      resetFields();
      this.setState({
        check_code: false
      });
    }
  };

  handleChangeCode = () => {
    const { setFieldsValue } = this.props.form;
    this.setState({ check_code: !this.state.check_code });
    if (this.state.check_code === false) {
      setFieldsValue({
        ["payment_category_code"]: null
      });
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        this.props.submit(values);
      }
    });
  };

  render() {
    const { getFieldDecorator, getFieldError } = this.props.form;
    const { paymentCategory } = this.props;
    const { loading } = paymentCategory;
    const { locations, cash_banks, accounts } = this.props.supplier;
    const error = paymentCategory.errorField ? paymentCategory.errorField : [];

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    const radioGroupStyle = {
      display: "flex",
      width: "100%"
    };

    const radioButtonStyle = {
      flex: "1",
      textAlign: "center"
    };

    return (
      <Modal
        title={<IntlMessages id="paymentCategories.button.add" />}
        visible={this.props.visible}
        centered
        onCancel={this.props.handleModal}
        onOk={this.handleSubmit}
        width="40%"
        okText={getLanguage({ id: "button.submit" })}
        cancelText={getLanguage({ id: "button.cancel" })}
        okButtonProps={{ loading: loading }}
        cancelButtonProps={{ loading: loading }}
      >
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          <div className="isoDetailDataFlex" style={{ padding: "0" }}>
            <InputGroup compact>
              <Col span={20}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="paymentCategories.add.code" />}
                  validateStatus={
                    error.payment_category_code ||
                    getFieldError("payment_category_code")
                      ? "error"
                      : null
                  }
                  help={
                    error.payment_category_code
                      ? error.payment_category_code
                      : getFieldError("payment_category_code")
                  }
                >
                  {getFieldDecorator("payment_category_code", {
                    //initialValue: data.payment_category_code,
                    rules: [
                      {
                        max: 30,
                        message: <IntlMessages id="maxLength30" />
                      }
                    ]
                  })(<Input disabled={this.state.check_code ? true : false} />)}
                </Form.Item>
              </Col>
              <Col span={4}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="autoGenerateCode" />}
                >
                  <Checkbox
                    style={{
                      paddingTop: "5px",
                      paddingLeft: "8px"
                    }}
                    checked={this.state.check_code}
                    onChange={this.handleChangeCode}
                  />
                </Form.Item>
              </Col>
            </InputGroup>

            <Form.Item {...formItemLayout} label={<IntlMessages id="name" />}>
              {getFieldDecorator("description", {
                // initialValue: data.description,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 50,
                    message: <IntlMessages id="maxLength50" />
                  }
                ]
              })(<Input />)}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="paymentTerms.add.default" />}
            >
              {getFieldDecorator("is_default", {
                initialValue: 0
              })(
                <Radio.Group
                  buttonStyle="solid"
                  style={{ display: "flex", width: "50%" }}
                >
                  <Radio.Button style={radioButtonStyle} value={1}>
                    <IntlMessages id="yes" />
                  </Radio.Button>
                  <Radio.Button style={radioButtonStyle} value={0}>
                    <IntlMessages id="no" />
                  </Radio.Button>
                </Radio.Group>
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="paymentType" />}
            >
              {getFieldDecorator("payment_type", {
                initialValue: 1,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Radio.Group
                  onChange={() => {
                    this.props.form.setFieldsValue({
                      id_account_direct_sales: null
                    });
                  }}
                  buttonStyle="solid"
                  style={radioGroupStyle}
                >
                  <Radio.Button style={radioButtonStyle} value={1}>
                    <IntlMessages id="cash" />
                  </Radio.Button>
                  <Radio.Button style={radioButtonStyle} value={2}>
                    <IntlMessages id="credit" />
                  </Radio.Button>
                  <Radio.Button style={radioButtonStyle} value={3}>
                    <IntlMessages id="card" />
                  </Radio.Button>
                  <Radio.Button style={radioButtonStyle} value={4}>
                    <IntlMessages id="other" />
                  </Radio.Button>
                </Radio.Group>
              )}
            </Form.Item>

            {this.props.form.getFieldValue("payment_type") === 1 ||
            this.props.form.getFieldValue("payment_type") === 3 ? (
              <Row>
                <Col span={24}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="sidebar.location" />}
                  >
                    {getFieldDecorator("id_location", {
                      //initialValue: data.id_account_direct_sales,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(
                      <Select
                        loading={this.props.supplier.loading}
                        showSearch
                        optionFilterProp="children"
                        onChange={() => {
                          this.props.form.setFieldsValue({
                            id_cash_bank: null
                          });
                        }}
                      >
                        {locations.map((data, key) => (
                          <Option key={key} value={data.id_location}>
                            {data.location_name}
                          </Option>
                        ))}
                      </Select>
                    )}
                  </Form.Item>
                </Col>
                <Col span={24}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="sidebar.cashBankReport" />}
                  >
                    {getFieldDecorator("id_cash_bank", {
                      //initialValue: data.id_account_direct_sales,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(
                      <Select
                        loading={this.props.supplier.loading}
                        showSearch
                        optionFilterProp="children"
                        notFoundContent={
                          this.props.supplier.loading ? (
                            <Spin size="small" />
                          ) : this.props.form.getFieldValue("id_location") ? (
                            <Empty description={<IntlMessages id="noData" />} />
                          ) : (
                            <Empty
                              description={<IntlMessages id="noLocation" />}
                            />
                          )
                        }
                      >
                        {cash_banks.map((data, key) => {
                          if (
                            this.props.form.getFieldValue("id_location") ===
                            data.id_location
                          ) {
                            return (
                              <Option key={key} value={data.id_cash_bank}>
                                {data.cash_bank_code +
                                  " (" +
                                  data.description +
                                  ")"}
                              </Option>
                            );
                          }
                        })}
                      </Select>
                    )}
                  </Form.Item>
                </Col>
              </Row>
            ) : (
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="accounts.pageHeader" />}
              >
                {getFieldDecorator("id_account_direct_sales", {
                  //initialValue: data.id_account_direct_sales,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Select
                    loading={this.props.supplier.loading}
                    showSearch
                    optionFilterProp="children"
                    //disabled={used ? true : false}
                  >
                    {accounts.map((data, key) => {
                      if (
                        (this.props.form.getFieldValue("payment_type") === 2 &&
                          data.id_account_type === "03") ||
                        (this.props.form.getFieldValue("payment_type") === 4 &&
                          data.id_account_type !== "02" &&
                          data.id_account_type !== "03")
                      ) {
                        return (
                          <Option key={key} value={data.id_account}>
                            {data.account_code + " (" + data.account_name + ")"}
                          </Option>
                        );
                      }
                    })}
                  </Select>
                )}
              </Form.Item>
            )}
          </div>
        </Form>
      </Modal>
    );
  }
}

const WrappedModalAddPaymentCategory = Form.create({
  name: "modal_add_payment_category"
})(ModalAddPaymentCategory);

export default WrappedModalAddPaymentCategory;
