import React, { Component } from "react";
import IntlMessages from "../../../../components/utility/intlMessages";
import getLanguage from "../../../App/MyLanguange";
import {
  Form,
  Input,
  Col,
  Modal,
  Radio,
  InputNumber,
  Divider,
  Select
} from "antd";

const InputGroup = Input.Group;
const Option = Select.Option;

class ModalAddTax extends Component {
  componentDidUpdate = () => {
    const { resetFields } = this.props.form;
    const { success } = this.props.tax;
    if (success) {
      resetFields();
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        values.percentage = values.percentage / 100;
        this.props.submit(values);
      }
    });
  };

  render() {
    const { getFieldDecorator, getFieldError } = this.props.form;
    const { tax } = this.props;
    const { loading } = tax;
    const error = tax.errorField ? tax.errorField : [];

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    const radioGroupStyle = {
      display: "flex",
      width: "100%"
    };

    const radioButtonStyle = {
      flex: "1",
      textAlign: "center"
    };

    return (
      <Modal
        title={<IntlMessages id="taxes.button.add" />}
        visible={this.props.visible}
        centered
        onCancel={this.props.handleModal}
        onOk={this.handleSubmit}
        width="40%"
        okText={getLanguage({ id: "button.submit" })}
        cancelText={getLanguage({ id: "button.cancel" })}
        okButtonProps={{ loading: loading }}
        cancelButtonProps={{ loading: loading }}
      >
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          {/* <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="taxes.add.subtitle.data" />
        </Divider> */}
          <div className="isoDetailDataFlex" style={{ padding: "0" }}>
            <div
              className="isoColumn-2 isoFormGroupComp"
              style={{ padding: "0 15px 0 0" }}
            >
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="taxes.add.name" />}
                validateStatus={
                  error.tax_name || getFieldError("tax_name") ? "error" : null
                }
                help={
                  error.tax_name ? error.tax_name : getFieldError("tax_name")
                }
              >
                {getFieldDecorator("tax_name", {
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    },
                    {
                      max: 50,
                      message: <IntlMessages id="maxLength50" />
                    }
                  ]
                })(
                  <Input
                  //disabled={used ? true : false}
                  //style={{ textTransform: "uppercase" }}
                  />
                )}
              </Form.Item>

              <InputGroup>
                <Col span={6}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="taxes.add.percentage" />}
                  >
                    {getFieldDecorator("percentage", {
                      initialValue: 0,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(
                      <InputNumber
                        // disabled={used ? true : false}
                        min={0}
                        max={100}
                        precision={2}
                        formatter={value => `${value}%`}
                        parser={value => value.replace("%", "")}
                        style={{ width: "100%" }}
                      />
                    )}
                  </Form.Item>
                </Col>
                <Col span={2} />
                <Col span={8}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="taxes.add.defaultSale" />}
                  >
                    {getFieldDecorator("default_sale", {
                      initialValue: 0
                    })(
                      <Radio.Group buttonStyle="solid" style={radioGroupStyle}>
                        <Radio.Button style={radioButtonStyle} value={1}>
                          <IntlMessages id="yes" />
                        </Radio.Button>
                        <Radio.Button style={radioButtonStyle} value={0}>
                          <IntlMessages id="no" />
                        </Radio.Button>
                      </Radio.Group>
                    )}
                  </Form.Item>
                </Col>
                <Col span={8}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="taxes.add.defaultPurchase" />}
                  >
                    {getFieldDecorator("default_purchase", {
                      initialValue: 0
                    })(
                      <Radio.Group buttonStyle="solid" style={radioGroupStyle}>
                        <Radio.Button style={radioButtonStyle} value={1}>
                          <IntlMessages id="yes" />
                        </Radio.Button>
                        <Radio.Button style={radioButtonStyle} value={0}>
                          <IntlMessages id="no" />
                        </Radio.Button>
                      </Radio.Group>
                    )}
                  </Form.Item>
                </Col>
              </InputGroup>
            </div>
          </div>
          <Divider orientation="left" style={{ fontSize: 14 }}>
            <IntlMessages id="taxes.add.subtitle.accounts" />
          </Divider>
          <div className="isoDetailDataFlex" style={{ padding: "0" }}>
            <div
              className="isoColumn-2 isoFormGroupComp"
              style={{ padding: "0 15px 0 0" }}
            >
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="taxes.add.salesAccount" />}
              >
                {getFieldDecorator("id_account_sale", {
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Select
                    loading={this.props.supplier.loading}
                    showSearch
                    optionFilterProp="children"
                  >
                    {this.props.supplier.accounts.map((data, key) =>
                      data.id_account_type === "10" ? (
                        <Option key={key} value={data.id_account}>
                          {data.account_code + " (" + data.account_name + ")"}
                        </Option>
                      ) : null
                    )}
                  </Select>
                )}
              </Form.Item>

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="taxes.add.purchaseAccount" />}
              >
                {getFieldDecorator("id_account_purchase", {
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Select
                    loading={this.props.supplier.loading}
                    showSearch
                    optionFilterProp="children"
                  >
                    {this.props.supplier.accounts.map((data, key) =>
                      data.id_account_type === "05" ? (
                        <Option key={key} value={data.id_account}>
                          {data.account_code + " (" + data.account_name + ")"}
                        </Option>
                      ) : null
                    )}
                  </Select>
                )}
              </Form.Item>
            </div>
          </div>
        </Form>
      </Modal>
    );
  }
}

const WrappedModalAddTax = Form.create({
  name: "modal_add_tax"
})(ModalAddTax);

export default WrappedModalAddTax;
