const actions = {
  getSuppliers: (page, search, filter) => ({
    type: "GET_SUPPLIERS_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter,
    }
  }),
  createSupplier: () => ({
    type: "CREATE_SUPPLIER_REQUEST",
  }),
  storeSupplier: data => ({
    type: "STORE_SUPPLIER_REQUEST",
    payload: { body: data }
  }),
  storeSupplierContact: data => ({
    type: "STORE_SUPPLIER_CONTACT_REQUEST",
    payload: { body: data }
  }),
  storeSupplierAddress: data => ({
    type: "STORE_SUPPLIER_ADDRESS_REQUEST",
    payload: { body: data }
  }),
  storeSupplierOpeningBalance: data => ({
    type: "STORE_SUPPLIER_OPENING_BALANCE_REQUEST",
    payload: {
      body: data
    }
  }),
  editSupplier: id_supplier => ({
    type: "EDIT_SUPPLIER_REQUEST",
    payload: { id_supplier: id_supplier }
  }),
  updateSupplier: (id_supplier, data) => ({
    type: "UPDATE_SUPPLIER_REQUEST",
    payload: {
      id_supplier: id_supplier,
      body: data
    }
  }),
  updateSupplierContact: (id_supplier_contact, data) => ({
    type: "UPDATE_SUPPLIER_CONTACT_REQUEST",
    payload: {
      id_supplier_contact: id_supplier_contact,
      body: data
    }
  }),
  updateSupplierAddress: (id_supplier_address, data) => ({
    type: "UPDATE_SUPPLIER_ADDRESS_REQUEST",
    payload: {
      id_supplier_address: id_supplier_address,
      body: data
    }
  }),
  updateSupplierOpeningBalance: (id_supplier_opening_balance, data) => ({
    type: "UPDATE_SUPPLIER_OPENING_BALANCE_REQUEST",
    payload: {
      id_supplier_opening_balance,
      body: data
    }
  }),
  showSupplier: id_supplier => ({
    type: "SHOW_SUPPLIER_REQUEST",
    payload: { id_supplier: id_supplier }
  }),
  deactivateSupplier: id_supplier => ({
    type: "DEACTIVATE_SUPPLIER_REQUEST",
    payload: { id_supplier }
  }),
  deactivateSupplierContact: id_supplier_contact => ({
    type: "DEACTIVATE_SUPPLIER_CONTACT_REQUEST",
    payload: { id_supplier_contact }
  }),
  deactivateSupplierAddress: id_supplier_address => ({
    type: "DEACTIVATE_SUPPLIER_ADDRESS_REQUEST",
    payload: { id_supplier_address }
  }),
  activateSupplier: id_supplier => ({
    type: "ACTIVATE_SUPPLIER_REQUEST",
    payload: { id_supplier }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
